export default function() {
  // currency tabs
  const ER_widget = document.querySelector('#ER-widget')
  const ER_tabs = ER_widget.querySelector('.ER-widget__tabs')
  const ER_list = ER_widget.querySelector('.ER-widget__list')
  const ER_items = ER_widget.querySelectorAll('.ER-widget__item')
  const ER_currency = ER_widget.querySelector('.ER-widget__currency')
  const ER_prev__item = ER_widget.querySelector('.ER-widget__control--prev')
  const ER_next__item = ER_widget.querySelector('.ER-widget__control--next')
  let _ER_listWidth = parseFloat(getComputedStyle(ER_list).width)
  let _itemWidth = parseFloat(getComputedStyle(ER_currency).width)
  const _items = []
  let _positionLeftItem = 0 // позиция левого элемента
  let _transform = 0 // значение транфсофрмации

  const _step = (_itemWidth / _ER_listWidth) * 100

  ER_items.forEach((item, index) => {
    _items.push({ item: item, position: index, transform: 0 })
  })

  const position = {
    getMin: 0,
    getMax: +_items.length - 1
  }

  const _transformItem = function(direction) {
    if (direction === 'right') {
      if (
        _positionLeftItem + _ER_listWidth / _itemWidth - 1 >=
        position.getMax
      ) {
        return
      }
      if (ER_prev__item.classList.contains('disabled')) {
        ER_prev__item.classList.remove('disabled')
      }
      if (
        !ER_next__item.classList.contains('disabled') &&
        _positionLeftItem + _ER_listWidth / _itemWidth >= position.getMax
      ) {
        ER_next__item.classList.add('disabled')
      }
      _positionLeftItem++
      _transform -= _step
    }
    if (direction === 'left') {
      if (_positionLeftItem <= position.getMin) {
        return
      }
      if (ER_next__item.classList.contains('disabled')) {
        ER_next__item.classList.remove('disabled')
      }
      if (
        !ER_prev__item.classList.contains('disabled') &&
        _positionLeftItem - 1 <= position.getMin
      ) {
        ER_prev__item.classList.add('disabled')
      }
      _positionLeftItem--
      _transform += _step
    }
    ER_list.style.transform = 'translateX(' + _transform + '%)'
  }

  const _controlPrevClick = function(e) {
    e.preventDefault()
    _transformItem('left')
  }
  const _controlNextClick = function(e) {
    e.preventDefault()
    _transformItem('right')
  }

  const _setUpListeners = () => {
    ER_prev__item.addEventListener('click', _controlPrevClick)
    ER_next__item.addEventListener('click', _controlNextClick)
  }
  const _deleteListeners = () => {
    ER_prev__item.removeEventListener('click', _controlPrevClick)
    ER_next__item.removeEventListener('click', _controlNextClick)
  }

  _setUpListeners()
  // currency tabs end

  // prev/next cards
  const cardsContainer = ER_widget.querySelector('.ER-widget__wrapper')
  const nextCards = ER_widget.querySelector('.ER-widget__next')
  const prevCards = ER_widget.querySelector('.ER-widget__prev')
  const transformCardsStep = 156
  let currentTranslateY = 0
  let cardsContainerHeight = parseFloat(getComputedStyle(cardsContainer).height)

  let maxTranslateY =
    2 * transformCardsStep -
    Math.floor(cardsContainerHeight / transformCardsStep) * transformCardsStep

  const _transformCards = function(direction) {
    if (direction === 'prev') {
      if (currentTranslateY === 0) {
        prevCards.classList.add('disable')
      } else {
        currentTranslateY += transformCardsStep
        nextCards.classList.remove('disable')
        currentTranslateY === 0
          ? prevCards.classList.add('disable')
          : prevCards.classList.remove('disable')
      }
    }
    if (direction === 'next') {
      if (currentTranslateY === maxTranslateY) {
        nextCards.classList.add('disable')
      } else {
        currentTranslateY -= transformCardsStep
        currentTranslateY === maxTranslateY
          ? nextCards.classList.add('disable')
          : nextCards.classList.remove('disable')

        prevCards.classList.remove('disable')
      }
    }
    cardsContainer.style.transform = 'translateY(' + currentTranslateY + 'px)'
  }

  const _controlPrevCards = function(e) {
    e.preventDefault()
    _transformCards('prev')
  }
  const _controlNextCards = function(e) {
    e.preventDefault()
    _transformCards('next')
  }

  const _setUpCardsListeners = () => {
    prevCards.addEventListener('click', _controlPrevCards)
    nextCards.addEventListener('click', _controlNextCards)
  }
  const _deleteCardsListeners = () => {
    prevCards.removeEventListener('click', _controlPrevCards)
    nextCards.removeEventListener('click', _controlNextCards)
  }
  _setUpCardsListeners()
}
