<template>
  <div id="ER-widget">
    <div class="ER-widget__header">
      <h2 class="ER-widget__title">
        Курс<span class="ER-widget__title_currency">eur</span> сегодня
      </h2>
      <div class="ER-widget__tabs">
        <ul class="ER-widget__list">
          <li class="ER-widget__item">
            <button
              class="ER-widget__currency"
              :class="renderBaseCurrency === currentCurrency ? 'active' : ''"
              v-on:click="activeCurrency(renderERs.base, renderERs.base)"
            >
              {{ renderERs.base }}
            </button>
          </li>
          <li
            class="ER-widget__item"
            v-for="rate of renderRates"
            :key="rate.id"
          >
            <button
              class="ER-widget__currency"
              :class="rate === currentCurrency ? 'active' : ''"
              v-on:click="activeCurrency(renderERs.base, rate)"
            >
              {{ rate }}
            </button>
          </li>
        </ul>
        <button class="ER-widget__control ER-widget__control--prev disabled">
          <svg
            width="8"
            height="12"
            viewBox="0 0 8 12"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
          >
