              d="M7.40991 10.58L2.82991 6L7.40991 1.41L5.99991 0L-8.7738e-05 6L5.99991 12L7.40991 10.58Z"
              fill="#787878"
            />
          </svg>
        </button>
        <button class="ER-widget__control ER-widget__control--next">
          <svg
            width="8"
            height="12"
            viewBox="0 0 8 12"
            fill="none"
            xmlns="http://www.w3.org/2000/svg"
          >
            <path
              d="M0.590088 10.58L5.17009 6L0.590088 1.41L2.00009 0L8.00009 6L2.00009 12L0.590088 10.58Z"
              fill="#282828"
            />
          </svg>
        </button>
      </div>
    </div>
    <div class="ER-widget__body">
      <div class="ER-widget__amount">
        <input
          class="ER-widget__input"
          type="number"
          name="amount"
          v-model="amount"
        />
        <span class="ER-widget__input_currency">{{ currentCurrency }}</span>
      </div>
      <div class="ER-widget__outer">
        <div class="ER-widget__wrapper">
          <div class="ER-widget__card" v-for="rate of newRates" :key="rate.id">
            <div class="ER-widget__card_header">
              {{ amount
              }}<span class="ER-widget__card_selected-currency"
                >{{ currentCurrency }} =</span
              >
            </div>
            <div class="ER-widget__card_value">
              {{ caclValue(amount, newERs.rates[rate])
              }}<span class="ER-widget__card_currency-to">{{ rate }}</span>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div class="ER-widget__footer">
      <button class="ER-widget__prev disable">
        <svg
          width="8"
          height="12"
          viewBox="0 0 8 12"
          fill="none"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path
            d="M7.40991 10.58L2.82991 6L7.40991 1.41L5.99991 0L-8.7738e-05 6L5.99991 12L7.40991 10.58Z"
            fill="#787878"
          />
        </svg>
        Назад
      </button>
      <button class="ER-widget__next">
        Далее
        <svg
          width="8"
          height="12"
          viewBox="0 0 8 12"
          fill="none"
          xmlns="http://www.w3.org/2000/svg"
        >
          <path
            d="M0.590088 10.58L5.17009 6L0.590088 1.41L2.00009 0L8.00009 6L2.00009 12L0.590088 10.58Z"
            fill="#282828"
          />
        </svg>
      </button>
    </div>
  </div>
</template>

<script>
import ER_Script from "../assets/ER_Script.js";
export default {
  name: "ERwidgetVue",
  data() {
    return {
      renderBaseCurrency: "AUD",
      renderERs: {},
      renderRates: {},
      newERs: {},
      newRates: {},
      amount: 1,
      isLoad: false,
      currentCurrency: "",
    };
  },
  async created() {
    try {
      const response = await fetch(
        `https://api.openrates.io/latest?base=${this.renderBaseCurrency}`
      );
      if (response.ok) {
        let json = await response.json();
        this.renderERs = this.newERs = json;
        this.renderRates = this.newRates = Object.keys(json.rates)
          .filter((rate) => rate !== this.renderBaseCurrency)
          .sort();
        this.currentCurrency = this.renderERs.base;
        this.isLoad = true;
      }
    } catch (err) {
      throw err;
    }
  },
  updated() {
    if (this.isLoad) {
      ER_Script();
      this.isLoad = false;
    }
  },
  methods: {
    caclValue(amount, rate) {
      return Math.floor(+amount * +rate * 100) / 100;
    },
    async activeCurrency(currentCur, newCurrency) {
      try {
        this.currentCurrency = newCurrency;
        const newResponse = await fetch(
          `https://api.openrates.io/latest?base=${this.currentCurrency}`
        );
        if (newResponse.ok) {
          let newJson = await newResponse.json();
          this.newERs = newJson;
          this.newRates = Object.keys(newJson.rates)
            .filter((rate) => rate !== this.currentCurrency)
            .sort();
        }
      } catch (err) {
        throw err;
      }
    },
  },
};
</script>

